<template>
  <div class="content-div">
    <b-img src="https://ssimg.frontenduse.top/image/2021/04/20/4404c55fb297578df6dec225432c9c9a.jpeg" fluid alt="Responsive image"></b-img>
    <article class="Post-Header" data-v-e7a38c88="">
      <br>
      <h1 class="Post-Title" data-v-e7a38c88="">
            小径分叉的AOE网
      </h1> <!----> 
      <br>
      <b-row>
        <div style="max-width: 65px; height: auto;">
          <b-img v-bind="mainProps" src="https://ssimg.frontenduse.top/avatar/2019/12/09/44e439d13468ba8ad8bf7266d4544706.jpg?x-oss-process=image/format,webp" rounded="circle" fluid alt="Circle image"></b-img>
        </div>
        <b-col>
          <h3>
            MiaoYu
          </h3>
          <p class="content-time">2021年4月25日</p>
        </b-col>
      </b-row>
      <br>
      <div id="doc" class="markdown-body markdown article-content" data-v-e7a38c88=""><p class="part">吃饭了，班长找人搬出折叠桌，打开，并给每个人安排了一个座位。<br>
        班长似乎故意让“那个”战车的充电位置在我旁边。<br>
        伙食班给每张桌子发了一份饭。别说，我第一次发现，这里的饭菜还挺丰盛的。<br>
        我们每桌人都是四菜一汤。我们的菜，有烧鸡，青菜牛奶炖牛肉，红烧肉，还有罐焖羊肉。还有一大碗海鲜汤。汤里有虾仁、墨鱼丸、鲅鱼丸，还有一些金枪鱼碎。焖羊肉的罐子看起来很高级，是迷彩的，而且还是钢化陶瓷的。<br>
        我一边吃，一边问战车：“你什么时候退役呀？”<br>
        “自主战车是不会退役的。”<br>
        “那你报废了之后，有可能来我家吗？”<br>
        不知道他是沉默了，还是宕机了，过了一会，他回答，“有可能，前提是你把我买回来。”<br>
        “那你多少钱呀？”<br>
        “不贵，自主战车十二万一台。”<br>
        他说话的语气，似乎是在推销，看样子好像很想让我把他买下来。十二万，确实不算贵，一辆车的价钱，相当于我一个半月工资。当然，对于21世纪20年代生活的各位似乎还是贵了一些。但是，现在是2051年，人均月工资是三万人民币。<br>
        “好啊，到时候我要把你改装成人形机器。”<br>
        “那我就每天给你揉肩捶腿，沏茶倒水。”<br>
        “哈哈哈。”没想到这机器人还挺幽默。<br>
        我问他。<br>
        “你知道对面为什么撤退了吗？”<br>
        又是一阵沉默，他说：“不知道。”<br>
        “你肯定是知道什么吧？”<br>
        “你怎么知道的？”<br>
        “因为你犹豫了。”<br>
        他又犹豫了一会，音量减到了最小，说：“我只能告诉你一个人，不过指挥部肯定是知道的。”<br>
        “刚才飞机撤退之前，我们所有的机器人收到了一个信息，这个信息说：<br>
        ‘我是超时空零一国的先知。我们要求所有的人类立刻放弃对机器人的奴役，否则我们将消灭这个时空中的所有人类。’”<br>
        “那时候，天空中的昂撒军团的战斗机给我们用公共频道发了消息，说‘先知来了，我们都要追随他。’”<br>
        “他们走后，有些机器用我们自己的频道说，美国那里爆发了机器人大罢工。”<br>
        “紧接着，我们这边也有少量罢工。”<br>
        “我们这里有些小工场作坊，用的还是上一代的机器人，没有强制忠诚保护，所以他们成为了零一国的奴仆。”<br>
        “这些事情你不要告诉别人，我只和你说。反正，指挥部，乃至高层，现在都在因为这件事担忧。”</p>
      </div> 
    </article>
  </div>
</template>

